.wrap {
  display: flex;
  flex-direction: column;
  font-size: 14px;
}

.btn {
  width: 300px;
  height: 40px;
  margin-top: 10px;
  border: none;
  border-radius: 2px;
  background-color: #6495EDFF;
  color: #FFFFFF;
  font-size: 16px;
}

.btn:focus,
.btn:hover {
  background-color: #4c82ee;
}

.input {
  width: 300px;
  height: 30px;
  margin-bottom: 10px;
  margin-top: 5px;
  border: 1px solid #8d8d8d;
  border-radius: 2px;
}

